.introducePage {
  background-color: black;
}

.column {
  box-sizing: border-box;
  position: fixed;
  padding: 50px;
  left: 0;
  transition: top 1s ease-in-out;
  background-color: black;
  color: white;

  &.about {
    p:first-child {
      font-size: 40px;
      font-weight: 600;
    }
  }

  &.skills {
    display: flex;
    flex-direction: column;

    p:first-child {
      font-size: 40px;
      font-weight: 600;
      margin-bottom: 50px;
    }

    .iconArea {
      width: 100%;
      height: 200px;
      perspective: 800px;

      .hexagon {
        transition: all 0.5s ease-in-out;
        transform-origin: center;
        backface-visibility: hidden;
      }
    }
  }
}

.columnNav {
  position: fixed;
  left: 20px;
  box-sizing: border-box;
  padding: 5px;
  width: 30px;
  border-radius: 20px;
  background-color: rgba($color: white, $alpha: 0.3);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  transition: top 1s ease-in-out;

  .page {
    border-radius: 100%;
    width: 15px;
    height: 15px;
    border: 2px solid white;
    margin: 7px 0px;
    cursor: pointer;
    box-sizing: border-box;
    transition: all 0.5s ease-out;
  }
}
